const slider = document.querySelector('.sliders-list');
const arrowBottom = document.querySelector('.arrow-bottom');
const arrowTop = document.querySelector('.arrow-top');
const sectionSlider = document.querySelector('.sliders');
const pagination = document.querySelectorAll('.circle');
const backgrounds = document.querySelectorAll('.background');

const sidebar = document.querySelector('#wrapper');
const btnSideBarClose = document.querySelector('#menu-close');
const hamburger = document.querySelector('#menu-toggle');

$('.carousel').carousel({
  interval: false
})

$('.slider-arrows').click(function () {
	let action = $(this).attr('data-slide');
    $('.carousel').carousel(action);
})

function showBlock(el, speed, prevEl = null) {
	el.style.display = 'block';
  let step = 1 / speed;
  let interval = setInterval(function() {
    if (+el.style.opacity >= 1)
      clearInterval(interval);
    if (prevEl) {
      prevEl.style.opacity = +prevEl.style.opacity - step; 
      if (+prevEl.style.opacity <= 0) 
        prevEl.style.display = "none";
    }
    el.style.opacity = +el.style.opacity + step;
    
  }, speed / 1000);
}

function toggleClass(item) {
	for(let i = 0; i < pagination.length; i++) {
		if (i !== item ) {
			pagination[i].classList.remove('active');
		} else
			pagination[i].classList.add('active');
	}
}

function moveSlider(direction = 'next') {
	let step = (direction == 'prev') ? -33.3 : 33.3;
	let transform = slider.dataset.transform - step;
	slider.dataset.transform = transform;
	switch (transform) {
		case 0:
			arrowTop.style.opacity = 0;
			arrowTop.style.zIndex = -100;
			arrowBottom.style.opacity = 1;
			arrowBottom.style.zIndex = 100;
			showBlock(backgrounds[0], 100, backgrounds[1]);
			toggleClass(0);

			break;
		case -33.3:
			arrowTop.style.opacity = 1;
			arrowTop.style.zIndex = 100;
			arrowBottom.style.opacity = 1;
			arrowBottom.style.zIndex = 100;
			(direction == 'prev') ? showBlock(backgrounds[1], 90, backgrounds[2]) : showBlock(backgrounds[1], 100, backgrounds[0]);
			toggleClass(1);
			break;
		case -66.6:
			arrowBottom.style.opacity = 0;
			arrowBottom.style.zIndex = -100;
			showBlock(backgrounds[2], 100, backgrounds[1]);
			toggleClass(2);
			break;
	}
	const resolution = (document.documentElement.clientWidth >= 1200) ? 'Y' : 'X';
	slider.style.transform = `translate${resolution}(${transform}%)`;
}

// Закрытие сайдбара
btnSideBarClose.addEventListener('click', () => {
  sidebar.classList.remove('toggled');
  btnSideBarClose.style.opacity = 0;
  hamburger.style.display = "block";
})

// Открытие гамбургера
hamburger.addEventListener('click', (evt) => {
  evt.stopPropagation();
  sidebar.classList.toggle('toggled');
  btnSideBarClose.style.opacity = 1;
  hamburger.style.display = "none";
})

// Закрытие гамбургера при клике в любое другое место
document.addEventListener('click', (evt) => {
  let target = evt.target;
  let itsHamburger = target == hamburger;
  let itsMenu = target == sidebar || sidebar.contains(target);
  let menuIsToggled = sidebar.classList.contains('toggled');

  if (!itsMenu && !itsHamburger && menuIsToggled) {
    sidebar.classList.remove('toggled');
    btnSideBarClose.style.opacity = 0;
  }
})

arrowBottom.addEventListener('click', () => {
	moveSlider()
})

arrowTop.addEventListener('click', () => {
	moveSlider('prev');
})


